import React, { useState, useEffect } from 'react';
import { Worker, Viewer } from '@react-pdf-viewer/core';
import '@react-pdf-viewer/core/lib/styles/index.css';
import { GlobalWorkerOptions } from 'pdfjs-dist/build/pdf';
import { db } from './firebase'; // Import Firestore configuration

// Set the worker source path directly from pdfjs-dist
GlobalWorkerOptions.workerSrc = `//cdnjs.cloudflare.com/ajax/libs/pdf.js/2.16.105/pdf.worker.min.js`;

function PDFViewer() {
  const [pdfUrl] = useState('/Lords.pdf'); // Ensure this file is exactly named and placed in the `public` folder
  const [currentPage, setCurrentPage] = useState(1);
  const [isAdmin] = useState(true); // Set this to true for admin, false for regular viewers

  // Real-time listener for Firestore changes
  useEffect(() => {
    const unsubscribe = db.collection('pdf-viewer').doc('currentPage').onSnapshot((doc) => {
      if (doc.exists) {
        setCurrentPage(doc.data().page);
      }
    });

    return unsubscribe; // Cleanup the listener on component unmount
  }, []);

  // Function to update the current page in Firestore
  const goToPage = (page) => {
    if (isAdmin) {
      // Update Firestore when admin changes the page
      db.collection('pdf-viewer').doc('currentPage').set({ page });
    }
    setCurrentPage(page); // Update local state
  };

  return (
    <div style={{ width: '100%', height: '600px' }}>
      <Worker workerUrl={GlobalWorkerOptions.workerSrc}>
        <Viewer 
          fileUrl={pdfUrl} 
          defaultScale={1.5} 
          onPageChange={(e) => goToPage(e.currentPage)} 
          initialPage={currentPage - 1} // Viewer is zero-indexed
        />
      </Worker>
      <div>
        <button onClick={() => goToPage(currentPage - 1)} disabled={currentPage <= 1}>Previous</button>
        <button onClick={() => goToPage(currentPage + 1)}>Next</button>
      </div>
    </div>
  );
}

export default PDFViewer;
